.fxch-footer {
    &-content {
        background-color: var(--b-footer-bg);
        color: var(--f-footer);
    }
    &-contacts {
        &-link {
            color: var(--f-footer);
            &:hover {
                color: var(--f-footer);
            }
            &-icon {
                color: var(--f-footer-secondary);
            }
        }
        &-copy {
            background-color: var(--b-footer-link-bg);
            color: var(--f-footer);
            &:hover {
                background-color: var(--b-footer-link-bg-hover);
            }
        }
        &-label {
            color: var(--f-toaster);
            background-color: var(--b-toaster-bg);
            border-color: var(--f-divider);
        }
    }
    &-social-item {
        color: var(--f-footer);
        border-color: var(--f-footer);
        background-color: rgba(0,0,0,0);
        &:hover {
            color: var(--b-footer-bg);
            background-color: var(--f-footer);
        }
    }
}