.fxch-header {
    border-color: var(--f-divider);
    background-color: var(--b-header);
    backdrop-filter: blur(40px);
    &-logo {
        background-color: var(--b-header-logo);
        &-img {
            .fxch-logo {
                --main-logo: var(--f-header-logo);
            }
        }
    }
    &-theme {
        color: var(--f-header-logo);
        background-color: var(--b-header-logo);
        &:hover {
            background-color: var(--b-header-logo-secondary);
        }
    }
    &-divider {
        background-color: var(--f-divider);
    }
    &-nav {
        &-link {
            color: var(--f-secondary-text);
            &:hover {
                color: var(--f-text);
            }
            &.fxch-header-nav-link {
                &--active {
                    color: var(--f-text);
                }
            }

        }
    }
    &-menu {
        &-dropdown {
            background-color: var(--b-card);
            box-shadow: var(--block-shadow);
        }
        &-language-item {
            .fxch-button {
                &.fxch-disabled {
                    &.fxch-active {
                        background-color: var(--accent-500);
                        color: var(--accent-500-contrast);
                    }
                }
            }
        }
        .fxch-button {
            .fxch-button-icon-wrap {
                background-color: transparent;
            }
        }
    }
    &-language {
        background-color: var(--accent-500-contrast);
        border-color: var(--accent-500);
        color: var(--accent-500);
        &:hover {
            background-color: var(--accent-500);
            color: var(--accent-500-contrast);
        }
        &.fxch-header-language {
            &--active {
                background-color: var(--accent-600);
                color: var(--accent-600-contrast);
            }
        }
        &-dropdown {
            background-color: var(--b-card);
            box-shadow: var(--block-shadow);
        }
    }
}