$breakpoints: (
    xs-h: "screen and (max-height: 670px)",
    sm-h: "screen and (max-height: 750px)",
    xs: "screen and (max-width: 600px)",
    sm: "screen and (max-width: 767px)",
    md: "screen and (max-width: 960px)",
    lg: "screen and (max-width: 1270px)",
    xl: "screen and (max-width: 1450px)"
) !default;

@mixin media-breakpoint($breakpointName) {
    $mediaQuery: map-get($breakpoints, $breakpointName);

    @if ($mediaQuery == null) {
        @content;
    } @else {
        @media #{$mediaQuery} {
            @content;
        }
    }
}