.toast {
    &-container {
        position: fixed;
        display: grid;
        grid-template-columns: 100%;
        row-gap: 10px;
        align-items: start;
        justify-items: end;
        pointer-events: none;
        z-index: 999999;
    }
    &-top-right {
        top: 12px;
        right: 12px;
    }
    &-custom {
        background: none !important;
        box-shadow: none !important;
        padding: 0 !important;
        margin: 0 !important;
        border-radius: 0 !important;
        overflow: visible !important;
        width: auto !important;
        min-width: 290px;
        max-width: 380px;
        pointer-events: auto;
        @include font-styles("text-sm");
        @include media-breakpoint("xs") {
            max-width: 300px;
        }
        .fxch-toast-text-wrap {
            a {
                text-decoration: underline;
                &:hover {
                    text-decoration: none;
                }
            }
            b {
                font-weight: 700;
            }
        }
    }
}