.fxch-team {
    &-header-actions-item {
        &--prev {
            .fxch-button {
                &:not(.fxch-disabled) {
                    &:hover {
                        .fxch-button-icon-inner {
                            transform: translateX(-1em) !important;   
                        }
                    }
                }
                .fxch-button-icon {
                    &-inner {
                        transform: translateX(0);
                    }
                }    
            }    
        }
        .fxch-button {
            &.fxch-button--icon {
                border-radius: 8px;
            }
        }
    }
}