@import "../common/mixin/mixins.mixin.scss";

.fxch-promo {
    &-title,
    &-text {
        color: var(--f-text);
    }
    &-decoration {
        &-block {
            border-color: var(--b-bg);
            border-color: var(--accent-500);
            box-shadow: 0px 0px 34.654px 0px rgba(20, 110, 244, 0.40);
            &:hover {
                box-shadow: 0px 0px 0px 0px rgba(20, 110, 244, 0.40);
            }
            &-icon {
                color: var(--f-text);
            }
        }
    }
}
