@import "../common/mixin/mixins.mixin.scss";

.fxch-news-card {
    background-color: var(--b-bg);
    box-shadow: 0px 0px 0px 0px rgba(45, 45, 45, 0.25);
    &:hover {
        background-color: var(--b-card);
        box-shadow: 0px 0px 60px 0px rgba(45, 45, 45, 0.25);
    }
    &--first {
        &:hover {
            .fxch-news-card-block {
                &:after {
                    background-color: var(--accent-500);
                }
            }
        }
    }
    &--second {
        &:hover {
            .fxch-news-card-block {
                &:after {
                    background-color: var(--d-color-first-500);
                }
            }
        }
    }
    &--third {
        &:hover {
            .fxch-news-card-block {
                &:after {
                    background-color: var(--d-color-second-500);
                }
            }
        }
    }
    &-block {
        border-color: var(--f-divider);
        &:after {
            background-color: rgba(0,0,0,0);
        }
    }
    &-body {
        &-title {
            color: var(--f-text);
        }
        &-text {
            color: var(--f-secondary-text);
        }
    }
    &-icon {
        color: var(--f-text);
    }
}
