.fxch-checkbox {
    &-fake {
        background-color: var(--b-hover);
        &:before {
            border-bottom-color: var(--accent-500-contrast);
            border-left-color: var(--accent-500-contrast);
        }
        &-wrap {
            &:after {
                background-color: var(--b-bg);
            }
        }
    }
    &-input {
        &:checked {
            & ~ .fxch-checkbox {
                &-fake {
                    &-wrap {
                        &:after {
                            background-color: var(--accent-400);
                        }
                        .fxch-checkbox {
                            &-fake {
                                background-color: var(--accent-500);
                            }
                        }
                    }
                }
            }
        }
    }
    .fxch-disabled {
        .fxch-checkbox {
            &-fake {
                background-color: var(--b-disabled);
            }
            &-text {
                &-wrap {
                    color: var(--f-disabled);
                }
            }
            &-input {
                &:checked {
                    & ~ .fxch-checkbox-fake-wrap {
                        .fxch-checkbox-fake {
                            background-color: var(--accent-400);
                        }
                    }
                }
            }
        }
    }
    label {
        &:hover {
            .fxch-checkbox {
                &-fake {
                    background-color: var(--b-focus);
                }
            }
        }
    }
}