.fxch-textarea {
    color: var(--f-text);
    &::-moz-placeholder {
        color: var(--f-secondary-text);
    }
    &:-ms-input-placeholder {
        color: var(--f-secondary-text);
    }
    &::-ms-expand {
        color: var(--f-secondary-text);
    }
    &::-webkit-input-placeholder {
        color: var(--f-secondary-text);
    }
    &:-webkit-autofill,
    &:-webkit-autofill:hover {
        -webkit-box-shadow: 0 0 0 1000px var(--b-bg) inset;
    }
    &-wrap {
        &.fxch-textarea-wrap {
            &--fill {
                background-color: var(--b-bg);
                border-color: var(--b-bg);
                &:not(.fxch-disabled) {
                    background-color: var(--b-bg);
                    border-color: var(--b-bg);
                    &:hover {
                        background-color: var(--b-hover);
                        border-color: var(--b-hover);
                    }
                    &:hover,
                    &:focus {
                        .fxch-textarea {
                            &::-moz-placeholder {
                                color: var(--f-text);
                            }
                            &:-ms-input-placeholder {
                                color: var(--f-text);
                            }
                            &::-ms-expand {
                                color: var(--f-text);
                            }
                            &::-webkit-input-placeholder {
                                color: var(--f-text);
                            }
                            &-label {
                                color: var(--f-text);
                            }
                        }
                    }
                    &.fxch-textarea-wrap--focus {
                        background-color: var(--b-hover);
                        border-color: var(--b-hover);
                    }
                }
                & ~ .fxch-textarea-hint {
                    color: var(--f-text);
                }
            }
            &--outline {
                background-color: var(--b-bg);
                border-color: var(--f-divider);
                &:not(.fxch-disabled) {
                    &:hover,
                    &.fxch-textarea-wrap--focus{
                        border-color: var(--f-text);
                        .fxch-textarea-label {
                            color: var(--f-text);
                        }
                    }
                    &.fxch-textarea-wrap--not-empty {
                        .fxch-textarea-icon {
                            color: var(--f-secondary-text);
                        }
                    }
                }
                .fxch-textarea-label-text {
                    background-color: var(--b-card);
                }
            }
            &--error {
                border-color: var(--warn-500) !important;
                .fxch-textarea-label {
                    color: var(--warn-500) !important;
                }
            }
        }
        &.fxch-disabled {
            .fxch-textarea {
                color: var(--f-disabled);
                &::-moz-placeholder {
                    color: var(--f-divider);
                }
                &:-ms-input-placeholder {
                    color: var(--f-divider);
                }
                &::-ms-expand {
                    color: var(--f-divider);
                }
                &::-webkit-input-placeholder {
                    color: var(--f-divider);
                }
                &-label {
                    color: var(--f-divider);
                }
                &-icon {
                    color: var(--f-disabled);
                }
            }
        }
    }
    &-label,
    &-icon {
        color: var(--f-secondary-text);
    }
    &-hint {
        color: var(--f-disabled);
    }
}