@font-face {
    font-family: 'ClashDisplay';
    src: url('/assets/fonts/ClashDisplay.woff2') format('woff2');
    font-weight: 500;
    font-display: swap;
    font-style: normal;
}
html,
body {
    width: 100%;
    height: 100%;
    position: fixed;
    overflow: visible !important;
    overscroll-behavior-y: none;
    -webkit-tap-highlight-color: transparent;
}
body {
    padding: 0;
    margin: 0;
    font-family: var(--font-global);
    font-optical-sizing: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
*,
*:before,
*:after {
    box-sizing: border-box;
}
* {
    &:focus-visible {
        outline: none;
    }
}
button,
input[type="email"],
input[type="tel"],
input[type="text"],
input[type="password"],
input[type="image"],
input[type="submit"],
input[type="button"],
input[type="search"],
textarea {
    font-family: var(--font-global);
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    outline: none;
}
.fxch-button,
.fxch-icon-button,
.fxch-stroked-button,
.fxch-flat-button {
    &::-moz-focus-inner,
    &:active,
    &:focus {
        outline: none;
        -moz-outline-style: none;
    }
}
.fxch-divider {
    &--horizontal {
        border-top-width: 1px;
        border-top-style: solid;
    }
    &--vertical {
        border-left-width: 1px;
        border-left-style: solid;
    }
}
html {
    &:not([data-useragent*="Macintosh"]) {
        body {
            &:not(.is-mobile) {
                .fxch-scroll {
                    &::-webkit-scrollbar {
                        -webkit-appearance: none;
                        &:vertical {
                            width: 17px;
                        }
                        &:horizontal {
                            height: 17px;
                        }
                    }
                    &::-webkit-scrollbar-thumb {
                        border-radius: 8px;
                        border-width: 3px;
                        border-style: solid;
                        transition:
                            background-color .5s cubic-bezier(.35,0,.25,1),
                            border-color .5s cubic-bezier(.35,0,.25,1);
                    }
                }
            }
        }
    }
}
