.fxch-tabs {
    .fxch-tabs {
        &-item {
            &.fxch-tabs-item--active {
                &:not(.fxch-disabled) {
                    color: var(--accent-500);
                }
            }
            &--stroked {
                &.fxch-tabs-item--active {
                    &:not(.fxch-disabled) {
                        .fxch-tabs-item-icon {
                            color: var(--accent-500);
                        }
                    }
                }
            }
        }
        &-ink-bar {
            border-bottom-color: var(--accent-500);
        }
    }
    &--slash {
        .fxch-tabs {
            &-item {
                &:not(.fxch-disabled) {
                    &:hover {
                        background-color: var(--b-hover);
                    }
                }
            }
        }
    }
    &-wrap {
        &--slash {
            border-bottom-color: var(--f-divider);
        }
        &:before,
        &:after {
            background-image: var(--vertical-shadow);
        }
    }
    &-item {
        background: rgba(0,0,0,.0);
        &.fxch-tabs-item--stroked {
            border-color: var(--f-divider);
            &:not(.fxch-disabled) {
                .fxch-tabs-item-icon {
                    color: var(--f-icon);
                }
            }
        }
        &:not(.fxch-disabled) {
            color: var(--f-secondary-text);
            &:hover {
                color: var(--f-text);
            }
        }
        &.fxch-disabled {
            color: var(--f-disabled);
        }
    }
}