.fxch-team {
    &-header {
        &-title {
            color: var(--f-secondary-text);
        }
        &-actions-item {
            .fxch-button {
                &:not(.fxch-disabled) {
                    &.fxch-button--flat {
                        background-color: var(--b-card);
                        &:hover {
                            background-color: var(--b-hover);
                        }
                    }
                }
                &.fxch-disabled {
                    &.fxch-button--flat {
                        background-color: var(--b-disabled);
                    }
                }
                .fxch-button-icon-wrap {
                    background-color: transparent;
                }
            }
        }
    }
}