.cdk-overlay-dark-backdrop {
    background-color: var(--b-modal-backdrop);
}
.fxch {
    &-dropdown,
    &-dialog {
        .cdk-dialog-container {
            background-color: var(--b-card);
            box-shadow: var(--menu-shadow);
        }
    }
    &-dialog {
        .cdk-dialog-container {
            background-color: var(--b-modal-bg);
        }
    }
}