.toast {
    &-custom {
        .fxch-toast {
            &--warning {
                background-color: var(--t-warn-300);
                border-color: var(--t-warn-500);
                color: var(--t-warn-300-contrast-2);
                box-shadow: var(--t-warn-shadow);
                .fxch-toast {
                    &-icon {
                        color: var(--t-warn-500-contrast);
                        &-wrap {
                            background-color: var(--t-warn-500);
                        }
                    }
                    &-title {
                        color: var(--t-warn-300-contrast);
                    }
                }
            }
            &--error {
                background-color: var(--t-error-300);
                border-color: var(--t-error-500);
                color: var(--t-error-300-contrast-2);
                box-shadow: var(--t-error-shadow);
                .fxch-toast {
                    &-icon {
                        color: var(--t-error-500-contrast);
                        &-wrap {
                            background-color: var(--t-error-500);
                        }
                    }
                    &-title {
                        color: var(--t-error-300-contrast);
                    }
                }
            }
            &--info {
                background-color: var(--t-info-300);
                border-color: var(--t-info-500);
                color: var(--t-info-300-contrast-2);
                box-shadow: var(--t-info-shadow);
                .fxch-toast {
                    &-icon {
                        color: var(--t-info-500-contrast);
                        &-wrap {
                            background-color: var(--t-info-500);
                        }
                    }
                    &-title {
                        color: var(--t-info-300-contrast);
                    }
                }
            }
            &--success {
                background-color: var(--t-success-300);
                border-color: var(--t-success-500);
                color: var(--t-success-300-contrast-2);
                box-shadow: var(--t-success-shadow);
                .fxch-toast {
                    &-icon {
                        color: var(--t-success-500-contrast);
                        &-wrap {
                            background-color: var(--t-success-500);
                        }
                    }
                    &-title {
                        color: var(--t-success-300-contrast);
                    }
                }
            }
            &-text-wrap {
                a {
                    color: inherit;
                    &:hover,
                    &:focus {
                        color: inherit;
                    }
                }
            }
            &-close {
                .fxch-new-button {
                    color: inherit !important;
                    &:not(.fxch-new-disabled) {
                        background-color: transparent;
                        &:hover,
                        &:focus {
                            background-color: var(--f-divider-base);
                        }
                    }
                    &-icon {
                        color: inherit;
                    }
                }
            }
        }
    }
}