.fxch-header {
    &-menu {
        &-modal {
            .cdk-dialog-container {
                padding: 20px 0;
            }
            .fxch-header-menu {
                &-language-item {
                    &:first-of-type {
                        .fxch-button {
                            border-top-left-radius: 2px;
                            border-bottom-left-radius: 2px;
                        }
                    }
                    &:last-of-type {
                        .fxch-button {
                            border-top-right-radius: 2px;
                            border-bottom-right-radius: 2px;
                        }
                    }
                    .fxch-button {
                        border-radius: 0;
                    }
                }
            }
        }
        .fxch-button {
            &.fxch-button--icon {
                min-width: 44px;
                height: 44px;
            }
            .fxch-button-icon {
                font-size: 22px;
                &-block {
                    font-size: 22px;
                }
                &-wrap {
                    font-size: 30px;
                }
            }
        }
    }
    &-language {
        &-modal {
            .fxch-header-language {
                &-item {
                    .fxch-button {
                        display: flex;
                        justify-content: flex-start;
                        border-radius: 0;
                        width: 100%;
                        &-text {
                            text-transform: uppercase;
                        }
                    }
                }
            }
        }
    }
}