.fxch-partners {
    &-title {
        color: var(--f-secondary-text);
    }
    &-box {
        --p-main: var(--f-secondary-text);
        --p-a: var(--f-secondary-text);
        --p-blocking: var(--f-secondary-text);
        --p-vultr: var(--f-secondary-text);
        --p-vultr1: var(--f-secondary-text);
        --p-vultr2: var(--f-secondary-text);
        --p-vultr3: var(--f-secondary-text);
        --p-vultr4: var(--f-secondary-text);
        --p-nulink: var(--f-secondary-text);
        --p-t-trader: var(--f-secondary-text);
        --p-t-trader1: var(--f-secondary-text);
        --p-t-trader2: var(--f-secondary-text);
        --p-c-star: var(--f-secondary-text);
        --p-c-star1: var(--f-secondary-text);
        --p-c-star2: var(--f-secondary-text);
        --p-c-star3: var(--f-secondary-text);
        --p-c-star4: var(--f-secondary-text);
        --p-c-star5: var(--f-secondary-text);
        --p-c-star6: var(--f-secondary-text);
        --p-c-star7: var(--f-secondary-text);
        --p-c-star8: var(--f-secondary-text);
        --p-c-star9: var(--f-secondary-text);
        --p-c-star10: var(--f-secondary-text);
        --p-c-star11: var(--f-secondary-text);
        --p-c-star12: var(--f-secondary-text);
        --p-c-star13: var(--f-secondary-text);
        --p-c-star14: var(--f-secondary-text);
        --p-c-star15: var(--f-secondary-text);
        --p-c-star16: var(--f-secondary-text);
        --p-c-star17: var(--f-secondary-text);
        --p-c-star18: var(--f-secondary-text);
        --p-c-star19: var(--f-secondary-text);
        --p-aws: var(--f-secondary-text);
        --p-aws1: var(--f-secondary-text);
    }
    &-img {
        &:hover {
            --p-main: var(--f-text);
            --p-a: #4338CA;
            --p-blocking: #49DDF0;
            --p-vultr: #17244B;
            --p-vultr1: #D0F5FD;
            --p-vultr2: #51B8FE;
            --p-vultr3: #027BFB;
            --p-vultr4: #027BFB;
            --p-nulink: #333333;
            --p-t-trader: #D53636;
            --p-t-trader1: #2D2D2D;
            --p-t-trader2: #F1B82B;
            --p-c-star: #fff;
            --p-c-star1: #1D2087;
            --p-c-star2: #1C258B;
            --p-c-star3: #193395;
            --p-c-star4: #1449A7;
            --p-c-star5: #0C6ABF;
            --p-c-star6: #0392DE;
            --p-c-star7: #009FE8;
            --p-c-star8: #fff;
            --p-c-star9: #00B8EE;
            --p-c-star10: #0ABBEF;
            --p-c-star11: #25C2F0;
            --p-c-star12: #50CEF3;
            --p-c-star13: #8CDFF7;
            --p-c-star14: #D9F4FC;
            --p-c-star15: #00B6ED;
            --p-c-star16: #0CB9EE;
            --p-c-star17: #2DC3F0;
            --p-c-star18: #61D2F4;
            --p-c-star19: #AAE7F9;
            --p-aws: #282F3D;
            --p-aws1: #EB9E3F;
        }
    }
}
html.dark {
    .fxch-partners-img:hover {
        --p-vultr: #027BFB;
        --p-nulink: var(--f-text);
        --p-t-trader1: var(--f-text);
        --p-aws: #8b9bbc;
    }
}