.fxch-team-card {
    background-color: var(--b-card);
    &-default {
        background-color: var(--b-card);
        &-logo {
            .fxch-logo {
                --main-logo: var(--f-secondary-text);
                --accent-logo: var(--f-third-text);
            }
        }
        &-header {
            &-name {
                color: var(--f-text);
            }
            &-job-title {
                color: var(--f-secondary-text);
            }
        }
        &-footer-item {
            background-color: var(--b-chips);
            color: var(--f-text);
        }
    }
    &-block {
        background-color: rgba(45, 45, 45, 0.80);
        backdrop-filter: blur(4px);
    }
    &-header {
        &-name {
            color: #fff;
        }
        &-job-title {
            color: rgba(255, 255, 255, 0.50);
        }
    }
    &-footer-item {
        background-color: rgba(255, 255, 255, 0.20);
        color: #fff;
    }
}