@import "../common/mixin/mixins.mixin.scss";

.fxch-about-card {
    background-color: var(--b-bg);
    box-shadow: 0px 0px 0px 0px rgba(45, 45, 45, 0.25);
    &.fxch-about-card--active {
        background-color: var(--b-card);
        box-shadow: var(--about-shadow);
        html.dark & {
            background-color: var(--b-about-card);
        }
    }
    &--first {
        &.fxch-about-card--active {
            html.dark & {
                box-shadow: var(--about-shadow-first-end);
            }
            .fxch-about-card-block {
                &:after {
                    background-color: var(--accent-500);
                }
            }
        }
        html.dark & {
            box-shadow: var(--about-shadow-first-start);
        }
    }
    &--second {
        &.fxch-about-card--active {
            html.dark & {
                box-shadow: var(--about-shadow-second-end);
            }
            .fxch-about-card-block {
                &:after {
                    background-color: var(--d-color-first-500);
                }
            }
        }
        html.dark & {
            box-shadow: var(--about-shadow-second-start);
        }
    }
    &--third {
        &.fxch-about-card--active {
            html.dark & {
                box-shadow: var(--about-shadow-third-end);
            }
            .fxch-about-card-block {
                &:after {
                    background-color: var(--d-color-second-500);
                }
            }
        }
        html.dark & {
            box-shadow: var(--about-shadow-third-start);
        }
    }
    &-block {
        border-color: var(--f-divider);
        &:after {
            background-color: rgba(0,0,0,0);
        }
    }
    &-body {
        &-title {
            color: var(--f-text);
        }
        &-text {
            color: var(--f-secondary-text);
        }
    }
}
