.fxch-technology {
    &-wrap {
        background-color: var(--b-technology);
        &--end {
            background-color: var(--b-bg);
        }
    }
    .fxch-technology {
        &-first {
            .fxch-technology {
                &-panel {
                    color: var(--f-technology-f-color-secondary);
                }
                &-title {
                    color: var(--f-technology-f-color); 
                }
            }
        }
        &-second {
            .fxch-technology {
                &-panel {
                    color: var(--accent-400-contrast-secondary);
                    background-color: var(--accent-400);
                }
                &-title {
                    color: var(--accent-400-contrast);
                }
            }
        }
    }
}