:root {
    // accent
    --accent-400: #156FF4;
    --accent-500: #146EF4;
    --accent-600: #125FD0;
    --accent-400-contrast: #fff;
    --accent-400-contrast-secondary: rgba(255, 255, 255, 0.50);
    --accent-500-contrast: #fff;
    --accent-600-contrast: #fff;
    // warn
    --warn-500: #F41414;
    // Foreground
    --f-base: #000;
    --f-text: #2D2D2D;
    --f-header-logo: #fff;
    --f-secondary-text: rgba(45, 45, 45, 0.50);
    --f-third-text: rgba(45, 45, 45, 0.25);
    --f-fourth-text: rgba(45, 45, 45, 0.1);
    --f-disabled: rgba(0, 0, 0, .30);
    --f-divider: rgba(0, 0, 0, .1);
    --f-technology-f-color: #EDEFF9;
    --f-technology-f-color-secondary: rgba(237, 239, 249, 0.5);
    --f-footer: #ffffff;
    --f-footer-secondary: rgba(255, 255, 255, 0.5);
    --f-toaster: #ffffff;
    // Background
    --b-card: #fff;
    --b-header: rgba(255, 255, 255, .8); 
    --b-header-logo: #2D2D2D;
    --b-header-logo-secondary: #232323;
    --b-card-secondary: rgba(237, 239, 249, 0.50);
    --b-bg: #EDEFF9;
    --b-hover: #cccccc;
    --b-focus: #c2c2c2;
    --b-disabled: rgba(0, 0, 0, .06);
    --b-chips: rgba(45, 45, 45, .05);
    --b-technology: #2D2D2D;
    --b-footer-bg: #2D2D2D;
    --b-footer-link-bg: rgba(255,255,255,.1);
    --b-footer-link-bg-hover: rgba(255,255,255,.05);
    --b-toaster-bg: rgba(45, 45, 45, .8);
    --b-modal-backdrop: rgba(45,45,45,.7);
    --b-modal-bg: #EDEFF9;

    --d-color-first-500: #7761ff;
    --d-color-first-600: #AC14F4;
    --d-color-second-500: #41D4A8;
    --d-color-second-600: #14F438;

    --menu-shadow: 0px 4px 10px rgba(0, 0, 0, .1);
    --block-shadow: 0px 10px 30px 0px rgba(0, 0, 0, 0.2);
    --box-shadow: 0px 0px 16px 0px rgba(45, 45, 45, 0.07);
    --about-shadow: 0px 0px 60px 0px rgba(45, 45, 45, 0.25);
    --vertical-shadow: linear-gradient(90deg, rgba(0, 0, 0, 0.1) 0%, rgba(0, 0, 0, 0) 100%);
    --carousel-shadow: linear-gradient(90deg, rgba(0, 0, 0, 0.2) 0%, rgba(0, 0, 0, 0) 100%);

    --font-global: "Rubik", sans-serif;
    --font-titles: 'ClashDisplay', sans-serif;
}

html.dark {
    --f-base: rgba(255, 255, 255, 1);
    --f-text: rgba(255, 255, 255, .9);
    --f-secondary-text: rgba(255, 255, 255, .56);
    --f-third-text: rgba(255, 255, 255, .28);
    --f-fourth-text: rgba(255, 255, 255, 0.05);
    --f-disabled: rgba(255, 255, 255, .3);
    --f-divider: rgba(225, 225, 225, .12);
    --f-technology-f-color: #ffffff;
    --f-technology-f-color-secondary: rgba(255, 255, 255, 0.5);

    --b-card: #424242;
    --b-card-secondary: rgba(0, 0, 0, 0.50);
    --b-header: rgba(255, 255, 255, .05);
    --b-bg: #2D2D2D;
    --b-hover: #222222;
    --b-focus: #181818;
    --b-disabled: rgba(0, 0, 0, .06);
    --b-chips: rgba(255, 255, 255, .05);
    --b-about-card: rgba(45, 45, 45, 0.10);
    --b-technology: #000000;
    --b-footer-bg: #383838;
    --b-footer-link-bg: rgba(0,0,0,.1);
    --b-footer-link-bg-hover: rgba(0,0,0,.05);
    --b-toaster-bg: #383838;
    --b-modal-backdrop: rgba(0,0,0,.7);
    --b-modal-bg: #2D2D2D;

    --about-shadow-first-start: 0px 0px 0px 0px rgba(20, 110, 244, 0.25);
    --about-shadow-first-end: 0px 0px 180px 0px rgba(20, 110, 244, 0.25);
    --about-shadow-second-start: 0px 0px 0px 0px rgba(119, 97, 255, 0.25);
    --about-shadow-second-end: 0px 0px 180px 0px rgba(119, 97, 255, 0.25);
    --about-shadow-third-start: 0px 0px 0px 0px rgba(65, 212, 168, 0.25);
    --about-shadow-third-end: 0px 0px 180px 0px rgba(65, 212, 168, 0.25);
}