@mixin font-styles($typography) {
    @if ($typography == 'h1') {
        font-family: var(--font-titles);
        font-size: 279px;
        line-height: 1;
        font-weight: 500;
    }
    @else if ($typography == 'h2') {
        font-family: var(--font-titles);
        font-size: 139px;
        line-height: 1;
        font-weight: 500;
    }
    @else if ($typography == 'h3') {
        font-family: var(--font-titles);
        font-size: 96px;
        line-height: 1.2;
        font-weight: 500;
    }
    @else if ($typography == 'label-md') {
        font-family: var(--font-titles);
        font-size: 68px;
        line-height: 1;
        font-weight: 500;
    }
    @else if ($typography == 'label-sm') {
        font-family: var(--font-titles);
        font-size: 56px;
        line-height: 1.2;
        font-weight: 500;
    }
    @else if ($typography == 'label-xs') {
        font-family: var(--font-titles);
        font-size: 48px;
        line-height: 1;
        font-weight: 500;
    }
    @else if ($typography == 'label') {
        font-family: var(--font-titles);
        font-size: 32px;
        line-height: 1;
        font-weight: 500;
    }
    @else if ($typography == 'text-xl') {
        font-size: 24px;
        line-height: 32px;
        font-weight: 400;
    }
    @else if ($typography == 'text-lg') {
        font-size: 20px;
        line-height: 1.4;
        font-weight: 400;
    }
    @else if ($typography == 'text-sm') {
        font-size: 16px;
        line-height: 22px;
        font-weight: 400;
    }
    @else if ($typography == 'text-xs') {
        font-size: 14px;
        line-height: 18px;
        font-weight: 400;
    }
}