body {
    color: var(--f-base);
    background-color: var(--b-bg);
}
a {
    color: var(--accent-500);
    &:hover {
        color: var(--accent-600);
    }
}
.fxch-divider {
    &--horizontal {
        border-top-color: var(--f-divider);
    }
    &--vertical {
        border-left-color: var(--f-divider);
    }
}
html {
    &:not([data-useragent*="Macintosh"]) {
        body {
            &:not(.is-mobile) {
                .fxch-scroll {
                    &::-webkit-scrollbar-thumb {
                        border-color: var(--b-card);
                        background-color: var(--b-hover);
                    }
                }
                .fxch-scroll {
                    &--accent {
                        &::-webkit-scrollbar-thumb {
                            background-color: var(--accent-400);
                            &:hover {
                                background-color: var(--accent-600);
                            }
                        }
                    }
                }
            }
        }
    }
}
