.fxch-input {
    color: var(--f-text);
    &::-moz-placeholder {
        color: var(--f-secondary-text);
    }
    &:-ms-input-placeholder {
        color: var(--f-secondary-text);
    }
    &::-ms-expand {
        color: var(--f-secondary-text);
    }
    &::-webkit-input-placeholder {
        color: var(--f-secondary-text);
    }
    &:-webkit-autofill,
    &:-webkit-autofill:hover {
        -webkit-box-shadow: 0 0 0 1000px var(--b-bg) inset;
    }
    &-wrap {
        &--focus {
            .fxch-input-clear {
                &:hover {
                    background-color: var(--b-hover);
                }
            }
        }
        &.fxch-input-wrap {
            &--fill {
                background-color: var(--b-bg);
                border-color: var(--b-bg);
                &:not(.fxch-disabled) {
                    background-color: var(--b-bg);
                    border-color: var(--b-bg);
                    &:hover {
                        background-color: var(--b-hover);
                        border-color: var(--b-hover);
                        .fxch-input {
                            &::-moz-placeholder {
                                color: var(--f-secondary-text);
                            }
                            &:-ms-input-placeholder {
                                color: var(--f-secondary-text);
                            }
                            &::-ms-expand {
                                color: var(--f-secondary-text);
                            }
                            &::-webkit-input-placeholder {
                                color: var(--f-secondary-text);
                            }
                        }
                    }
                    &:hover,
                    &:focus {
                        .fxch-input {
                            &-label,
                            &-icon,
                            &-clear,
                            &-info {
                                color: var(--f-text);
                            }
                        }
                    }
                    &.fxch-input-wrap--focus {
                        background-color: var(--b-hover);
                        border-color: var(--b-hover);
                        .fxch-input {
                            &-label,
                            &-icon,
                            &-clear {
                                color: var(--f-text);
                            }
                        }
                    }
                }
                & ~ .fxch-input-hint {
                    color: var(--f-text);
                }
            }
            &--outline {
                background-color: var(--bg);
                border-color: var(--f-divider);
                &:not(.fxch-disabled) {
                    &:hover,
                    &.fxch-input-wrap--focus {
                        border-color: var(--f-text);
                    }
                    &.fxch-input-wrap--not-empty {
                        .fxch-input-icon {
                            color: var(--f-secondary-text);
                        }
                    }
                }
                .fxch-input-label-text {
                    background-color: var(--b-card);
                }
            }
            &--error {
                border-color: var(--warn-500) !important;
                .fxch-input-label {
                    color: var(--warn-500) !important;
                }
            }
        }
        &.fxch-disabled {
            .fxch-input {
                color: var(--f-disabled);
                &::-moz-placeholder {
                    color: var(--f-divider);
                }
                &:-ms-input-placeholder {
                    color: var(--f-divider);
                }
                &::-ms-expand {
                    color: var(--f-divider);
                }
                &::-webkit-input-placeholder {
                    color: var(--f-divider);
                }
                &-label {
                    color: var(--f-divider);
                }
                &-icon,
                &-clear {
                    color: var(--f-disabled);
                }
            }
        }
    }
    &-icon,
    &-clear,
    &-label {
        color: var(--f-secondary-text);
    }
    &-clear {
        background-color: transparent;
        &:hover {
            background-color: var(--b-hover);
        }
    }
    &-hint {
        color: var(--f-disabled);
    }
}