.fxch-button {
    &.fxch-disabled {
        color: var(--f-text);
        background-color: rgba(255,255,255,0);
        &.fxch-button {
            &--accent {
                background-color: var(--b-disabled);
            }
        }
    }
    &:not(.fxch-disabled) {
        background-color: rgba(255,255,255,0);
        color: var(--f-base);
        &.fxch-button {
            &--accent {
                color: var(--accent-500);
                &:hover {
                    background-color: var(--b-hover);
                }
                &.fxch-active {
                    background-color: var(--b-hover);
                }
            }
        }
        &:hover {
            background-color: var(--b-hover);
        }
        &.fxch-active {
            background-color: var(--b-hover);
        }
    }
    &.fxch-button {
        &--flat {
            &:not(.fxch-disabled) {
                background-color: var(--b-bg);
                color: var(--f-text);
                &:hover {
                    background-color: var(--b-hover);
                }
                &.fxch-active {
                    background-color: var(--b-hover);
                }
                &.fxch-button {
                    &--accent {
                        background-color: var(--accent-500);
                        color: var(--accent-500-contrast);
                        box-shadow:
                            0px 10px 16px 0px rgba(255, 255, 255, 0.08) inset,
                            0px 1px 1px 0px rgba(255, 255, 255, 0.25) inset;
                        &:hover {
                            background-color: var(--accent-600);
                            color: var(--accent-600-contrast);
                            box-shadow: 
                                0px 10px 16px 0px rgba(255, 255, 255, 0) inset,
                                0px 1px 1px 0px rgba(255, 255, 255, 0) inset;
                        }
                        &.fxch-active {
                            background-color: var(--accent-600);
                            color: var(--accent-600-contrast);
                            box-shadow: 
                                0px 10px 16px 0px rgba(255, 255, 255, 0) inset,
                                0px 1px 1px 0px rgba(255, 255, 255, 0) inset;
                        }
                    }
                }
            }
        }
    }
    &:not(.fxch-disabled) {
        .fxch-button {
            &-icon{
                color: var(--f-text);
            }
        }
    }
    &-text {
        color: currentColor;
    }
    &-icon {
        &-wrap {
            background-color: rgba(8, 8, 8, 0.20);
        }
    }
    &:not(.fxch-button--default) {
        .fxch-button {
            &-icon,
            &-trigger-icon {
                color: currentColor;
            }
        }
    }
}