.fxch-about-card {
    &:hover {
        .fxch-decor {
            &-circles-item {
                --translate: 12px;
                @include media-breakpoint("lg") {
                    --translate: 6px;
                }
                &:nth-of-type(2) {
                    transform: translateX(var(--translate));
                }
                &:nth-of-type(3) {
                    transform: translateX(calc(var(--translate) * 2));
                }
                &:nth-of-type(4) {
                    transform: translateX(calc(var(--translate) * 3));
                }
                &:nth-of-type(5) {
                    transform: translateX(calc(var(--translate) * 4));
                }
            }
            &-ring {
                transform: rotateY(180deg);
            }
            &-target {
                --ring-size: 4.6em;
                --dot-size: 1.2em;
                &-ring {
                    border-width: 1em;
                    opacity: 1;
                }
                &-dot {
                    border-width: .6em;
                    opacity: 1;
                }
            }
        }
    }
}
